<template>
  <div>
    <div class="audio_box_s">
      <div>
        <div class="audiotitlex">
          <div class="img_box">
            <img class="img_box" src="@/assets/icon/file-text.png" />
          </div>
          <div class="fileName_box">
            {{ fileName }}
          </div>
        </div>
      </div>
      <iframe
        class="iframe"
        :src="goodsFilePath"
        frameborder="0"
        width="99%"
      ></iframe>
    </div>
  </div>
</template>
<script>
import {
  selectTimeParameter, //获取调用阅读时长接口频率
  updateOcDownloadReading, //更新用户阅读时长
  selectFileLinkAndReadingLocation, //查看阅读文件地址和文件进度
} from "@/api/special/special";
import { fileURL } from "@/utils/config";
import { videoPlayer } from "vue-video-player";
import { checkIsBuy, goodsInfoDetail } from "@/api/bookstore/bookstore";
import "video.js/dist/video-js.css";
import audioPlay from "@/views/onlineReading/mobileClient/components/audioPlay1.vue";
export default {
  components: {
    videoPlayer,
    audioPlay,
  },
  data() {
    return {
      fileName: "",
      goodsFileId: undefined,
      goodsId: this.$route.query.goodsId, //需要传递的参数
      filesId: "", //需要传递的参数(资源文件id)
      relevanceId: undefined,
      resource: undefined,
      resourceType: undefined,
      teachInstId: undefined,
      goodsFilePath: "", //需要传递的参数(资源文件路径)
      readdefault: 0, //默认阅读第一个
      loading: false,
      play: false,
      setInterval2: undefined,
      TimeParameter: 10, //更新频次-单位分钟
    };
  },
  props: {},
  created() {},
  destroyed() {
    this.closeTime();
  },
  methods: {
    selectTimeParameter() {
      //获取更新 时长和进度 的频次
      selectTimeParameter().then((res) => {
        this.TimeParameter = res.data;
        this.setInterval2 = setInterval(() => {
          this.updateOcDownloadReading(); //更新阅读时长
        }, this.TimeParameter * 60000);
      });
    },
    closeTime() {
      clearInterval(this.setInterval2);
      this.setInterval2 = null;
    },
    updateOcDownloadReading() {
      //更新用户阅读时长接口
      updateOcDownloadReading({
        goodsFileId: this.filesId,
        goodsId: this.$route.query.goodsId,
        relevanceId: this.$route.query.relevanceId,
        resource: this.$route.query.resource,
        resourceType: this.$route.query.resourceType,
        teachInstId: this.$route.query.teachInstId,
      }).then((res) => {});
    },
    tabbook(item) {
      this.loading = this.$loading({
        lock: true,
      });
      this.goodsFileId = item.filesId;
      this.fileName = item.sourceName;
      this.filesId = item.filesId;
      selectFileLinkAndReadingLocation({
        goodsFileId: item.filesId,
        fileType: item.fileType,
        goodsId: this.$route.query.goodsId,
        relevanceId: this.$route.query.relevanceId,
        resource: this.$route.query.resource,
        resourceType: this.$route.query.resourceType,
        teachInstId: this.$route.query.teachInstId,
      }).then((res) => {
        this.loading.close();
        let data = res.data;
        if (data) {
          this.buys(item, data);
        }
      });
    },
    buys(item, data) {
      //判断是否购买该商品
      this.payType = this.$route.query.payType
        ? this.$route.query.payType
        : "goods";
      checkIsBuy({
        relevanceId: this.goodsId,
        type: this.payType,
      }).then((res) => {
        if (data.hasPermission) {
          //有权限
          let goodsFilePath = data.filePath;
          let url =
            fileURL +
            "/goods_file" +
            goodsFilePath.split("goods_file")[1].split(".")[0];
          this.goodsFilePath = url;
          this.selectTimeParameter(); //获取更新进度和时长的频次   循环调用更新时长和更新进度方法
        } else if (res.data == "1" && item.sourceStatus == "2") {
          this.Trial = true; //试读
          this.goodsFilePath = data.filePath;
          let url =
            fileURL +
            "/goods_file" +
            goodsFilePath.split("goods_file")[1].split(".")[0];
          this.goodsFilePath = url;
        } else {
          //无权限
          this.goodsFilePath = "";
          this.checkIsBuys();
        }
      });
    },

    checkIsBuys() {
      //无权限走购买流程
      this.$alert("该文件需要购买后才能阅读，是否确认购买", "购买", {
        confirmButtonText: "立即购买",
        callback: (action) => {
          if (action === "cancel") {
            return;
          }
          let userId = this.$store.getters.userId;
          if (!userId) {
            this.$store.commit("SET_REDIRECTPARAMS", {
              goodsId: this.$route.query.goodsId,
              relevanceId: this.$route.query.relevanceId,
              resource: this.$route.query.resource,
              resourceType: this.$route.query.resourceType,
            });
            this.$router.push({
              path: "/login",
              query: {
                redirectPath: "/onlineReading",
              },
            });
          } else {
            //判断是否购买该商品
            this.payType = this.$route.query.payType
              ? this.$route.query.payType
              : "goods";
            checkIsBuy({
              relevanceId: this.goodsId,
              type: this.payType,
            }).then((res) => {
              console.log("是否已购买响应", res);
              if (res.data === "2") {
                this.$message.error("数据异常");
                return;
              }
              if (res.data === "1") {
                this.$router.push({
                  path: "/submitOrder/index",
                  query: {
                    id: this.$route.query.goodsId,
                    from: "bookstore",
                  },
                });
              } else {
                this.$message("该商品已购买无需重复购买");
              }
            });
          }
        },
      });
    },
  },
};
</script>

