<template>
  <div>
    <div class="read_center_box" id="read_center_box">
      <p class="dqyd">
        <img src="@/assets/icon/read_ico_sj.png" /> 您已阅读{{
          readingTimeCount | stohms
        }}<!-- 1小时23分钟54秒 -->
      </p>
      <div id="box">
        <div
          id="viewer"
          style="padding: 20px; box-sizing: border-box"
          v-show="boxViewer"
        >
          <div class="flipbook" v-if="wsitch">
            <div v-for="value in PdfItems">
              <div class="gradient"></div>
              <img :src="'http://' + value" />
            </div>
          </div>
        </div>
        <!-- 				<div style="position: relative;left: 0; bottom: 0;" id="slider-bar" class="turnjs-slider">
					<div id="slider"></div>
				</div>
				<div class="yema">
					{{PdfIndex}}/{{PdfSize}}
				</div> -->
      </div>
    </div>
    <div class="read_right_box">
      <div class="read_right_box1">
        <el-popover
          placement="left"
          width="350"
          trigger="click"
          v-model="shezhivisible"
        >
          <div class="shezhi">
            <i class="el-icon-error" @click="shezhivisible = false"></i>
            <li>
              <p>阅读主题</p>
              <div class="xz_box ydzt">
                <span
                  v-for="(item, index) in backgroundList"
                  :key="index"
                  :class="item.backclass === backclass ? 'active' : ''"
                  :style="'background: ' + item.color"
                  @click="changezt(item), result(), shezhisave()"
                ></span>
              </div>
            </li>
          </div>
          <li slot="reference">
            <img
              v-if="backclass !== 'zt_heise'"
              src="@/assets/icon/read_ico_shezhi.png"
            />
            <img
              v-if="backclass === 'zt_heise'"
              src="@/assets/icon/read_ico_shezhi_bai.png"
            />
            <p>设置</p>
          </li>
        </el-popover>
        <li
          :class="
            this.$route.query.typeName == 0 && isBookshelf === true
              ? 'cursorDefault'
              : ''
          "
          @click="addBookshelf()"
        >
          <img
            v-if="backclass !== 'zt_heise'"
            src="@/assets/icon/read_ico_shujia.png"
          />
          <img
            v-if="backclass === 'zt_heise'"
            src="@/assets/icon/read_ico_shujia_bai.png"
          />
          <!-- <p :class="this.$route.query.typeName == 0 ? 'cursorDefault' : ''">
            {{ isBookshelf ? "已加书架" : "加入书架" }}
          </p> -->
          <p v-if="isBookshelf">已加书架</p>
          <p v-if="!isBookshelf">加入书架</p>
        </li>
        <li @click="fullScreen">
          <img
            v-if="backclass !== 'zt_heise'"
            src="@/assets/icon/read_ico_quanping.png"
          />
          <img
            v-if="backclass === 'zt_heise'"
            src="@/assets/icon/read_ico_shujia_bai.png"
          />
          <p>全屏阅读</p>
        </li>
      </div>
      <div class="read_right_box2">
        <div id="prev">
          <img
            @click="previous()"
            :style="PdfIndex === 1 ? 'visibility:hidden' : ''"
            src="@/assets/icon/read_ico_shang.png"
          />
        </div>
        <div id="next">
          <img
            @click="next()"
            :style="PdfIndex === PdfSize ? 'visibility:hidden' : ''"
            src="@/assets/icon/read_ico_xia.png"
          />
        </div>
      </div>
    </div>
    <div class="slider_box" v-if="PdfSize > 1">
      <el-slider
        :min="1"
        v-model="PdfIndex"
        @change="onProgressChange"
        :max="PdfSize"
      >
      </el-slider>
    </div>
  </div>
</template>

<script>
import jqueryui from "/public/js/jquery-ui.js";
import turn from "/public/js/turn.min.js";
import basic from "/public/js/basic.js";
import {
  selectFileLinkAndReadingLocation, //查看阅读文件地址和文件进度
  selectReadInfo, //获取阅读时长
  selectTimeParameter, //获取调用阅读时长接口频率
  updateOcDownloadReading, //更新用户阅读时长
  updateOcReadSchedule, //更新阅读进度
  getConvertImagePath, //获取转换后的图片路径（pdf在线阅读）
} from "@/api/special/special";
import {
  IsBookshelfAdd,
  BookshelfAdd,
  delBookshelf,
  checkIsBuy,
  SelGoodsStatus,
} from "@/api/bookstore/bookstore";
import screenfull from "screenfull";
import { number } from "echarts";
export default {
  components: {},
  data() {
    return {
      goodsId: this.$route.query.goodsId, //需要传递的参数
      filesId: "", //需要传递的参数(资源文件id)
      goodsFilePath: "", //需要传递的参数(资源文件路径)
      backclass: "zt_moren", //主题的背景颜色class
      shezhivisible: false,
      backgroundList: [
        {
          backclass: "zt_moren",
          color: "#f8f4e9",
        },
        {
          backclass: "zt_niupizhi",
          color: "#dbcfa5",
        },
        {
          backclass: "zt_danlvse",
          color: "#ebf1e5",
        },
        {
          backclass: "zt_danlanse",
          color: "#e9f5f5",
        },
        {
          backclass: "zt_danfense",
          color: "#f4e9e9",
        },
        {
          backclass: "zt_huise",
          color: "#e6e2e2",
        },
        {
          backclass: "zt_heise",
          color: "#424242",
        },
      ],
      bookdetail: {}, //基本信息
      sourceList: [], //文件信息
      readdefault: 0, //默认阅读第一个
      TimeParameter: 10, //更新频次-单位分钟
      readingTimeCount: 0, //阅读时间-单位秒
      setInterval2: undefined,
      setInterval1: undefined,
      PdfItems: [],
      PdfIndex: 1, //当前页数
      PdfSize: 1, //pdf个数
      slider: "",
      loading: false,
      item: undefined,
      index: undefined,
      wsitch: true,
      isBookshelf: false,
      TrialPdfSize: "", //试读
      Trial: false,
      boxViewer: true,
    };
  },
  created() {},
  destroyed() {
    this.closeTime();
  },
  methods: {
    tabbook(item, index) {
      this.Trial = false; //试读
      this.IsBookshelfAdd();
      this.item = item;
      this.index = index;
      this.loading = this.$loading({
        lock: true,
      });
      if (localStorage.getItem("shezhisave")) {
        var newshezhi = JSON.parse(localStorage.getItem("shezhisave"));
        this.num = newshezhi.num;
        this.zwzt = newshezhi.zwzt;
        this.backclass = newshezhi.backclass;
        this.$store.state.read.backclass = newshezhi.backclass;
      }
      selectFileLinkAndReadingLocation({
        goodsFileId: item.filesId, //goodsFileId没有，谢萌说可用filesId
        fileType: item.fileType,
        goodsId: this.$route.query.goodsId,
        relevanceId: this.$route.query.relevanceId,
        resource: this.$route.query.resource,
        resourceType: this.$route.query.resourceType,
        teachInstId: this.$route.query.teachInstId,
      }).then((res) => {
        let data = res.data;
        if (data) {
          this.buys(item, index, data);
        }
      });
    },
    buys(item, index, data) {
      //判断是否购买该商品
      this.payType = this.$route.query.payType
        ? this.$route.query.payType
        : "goods";
      checkIsBuy({
        relevanceId: this.goodsId,
        type: this.payType,
      }).then((res) => {
        if (data.hasPermission) {
          //有权限
          this.readdefault = index;
          this.boxViewer = true;
          this.goodsFilePath = data.filePath;
          this.initbook(item, index, data.readingLocation); //加载阅读方法并且跳转到阅读位置  readingLocation为阅读位置标记
          this.selectTimeParameter(); //获取更新进度和时长的频次   循环调用更新时长和更新进度方法
        } else if (res.data == "1" && item.sourceStatus == "2") {
          this.Trial = true; //试读
          this.boxViewer = true;
          this.readdefault = index;
          this.goodsFilePath = data.filePath;
          this.initbook(item, index, data.readingLocation); //加载阅读方法并且跳转到阅读位置  readingLocation为阅读位置标记
        } else {
          //无权限
          this.goodsFilePath = "";
          this.boxViewer = false;
          this.checkIsBuys();
        }
      });
    },
    checkIsBuys() {
      //无权限走购买流程
      let title = "";
      if (this.Trial == false) {
        title = "该文件需要购买后才能阅读，是否确认购买";
      } else {
        title = "购买后可继续阅读";
      }
      this.loading.close();
      this.$alert(`${title}`, "购买", {
        confirmButtonText: "立即购买",
        callback: (action) => {
          if (action === "cancel") {
            return;
          }
          let userId = this.$store.getters.userId;
          if (!userId) {
            // let paramsJson = JSON.stringify({ goodsId: this.goodsId, goodsType: this.goodsType })
            this.$store.commit("SET_REDIRECTPARAMS", {
              goodsId: this.$route.query.goodsId,
              relevanceId: this.$route.query.relevanceId,
              resource: this.$route.query.resource,
              resourceType: this.$route.query.resourceType,
            });
            this.$router.push({
              path: "/login",
              query: {
                redirectPath: "/onlineReading",
                // queryParams: paramsJson,
              },
            });
          } else {
            //判断是否购买该商品
            this.payType = this.$route.query.payType
              ? this.$route.query.payType
              : "goods";
            checkIsBuy({
              relevanceId: this.goodsId,
              type: this.payType,
            }).then((res) => {
              console.log("是否已购买响应", res);
              if (res.data === "2") {
                this.$message.error("数据异常");
                return;
              }
              if (res.data === "1") {
                this.$router.push({
                  path: "/submitOrder/index",
                  query: {
                    id: this.$route.query.goodsId,
                    from: "bookstore",
                  },
                });
              } else {
                this.$message("该商品已购买无需重复购买");
              }
            });
          }
        },
      });
    },
    IsBookshelfAdd() {
      //判断是否加入书架
      IsBookshelfAdd({
        goodsId: this.$route.query.goodsId,
        type: this.$route.query.resourceType,
      }).then((res) => {
        if (res.code == 200) {
          this.isBookshelf = res.data;
        }
      });
    },
    selectTimeParameter() {
      //获取更新 时长和进度 的频次
      selectTimeParameter().then((res) => {
        this.TimeParameter = res.data;
        this.setInterval2 = setInterval(() => {
          this.updateOcDownloadReading(); //更新阅读时长
          this.updateOcReadSchedule(); //更新阅读进度
        }, this.TimeParameter * 60000);
      });
    },
    onProgressChange(page) {
      if (Number(this.TrialPdfSize) < page && this.Trial) {
        this.PdfIndex = Number(this.TrialPdfSize);
        this.$message("该商品试读结束");
        this.checkIsBuys();
        return;
      }
      //翻页方法
      $(".flipbook").turn("page", this.PdfIndex);
      // 容器元素.turn(‘page’, 页码)
    },
    selectReadInfo() {
      //获取阅读时长
      selectReadInfo({
        filesId: this.filesId,
      }).then((res) => {
        if (res.data === null) {
          this.readingTimeCount = 0;
        } else {
          this.readingTimeCount = res.data.readingTimeCount * 60;
        }
        this.setInterval1 = setInterval(() => {
          this.readingTimeCount++;
        }, 1000);
      });
    },
    closeTime() {
      clearInterval(this.setInterval1);
      clearInterval(this.setInterval2);
      this.setInterval1 = null;
      this.setInterval2 = null;
    },
    updateOcDownloadReading() {
      //更新用户阅读时长接口
      updateOcDownloadReading({
        goodsFileId: this.filesId,
        goodsId: this.$route.query.goodsId,
        relevanceId: this.$route.query.relevanceId,
        resource: this.$route.query.resource,
        resourceType: this.$route.query.resourceType,
        teachInstId: this.$route.query.teachInstId,
      }).then((res) => {});
    },
    updateOcReadSchedule() {
      //更新阅读进度
      updateOcReadSchedule({
        fileId: this.filesId,
        readingLocation: this.PdfIndex, //阅读进度
      }).then((res) => {});
    },
    initbook(item, index, readingLocation) {
      //获取pdf图片list
      this.readdefault = index;
      this.filesId = item.filesId;
      this.wsitch = false;
      setTimeout(() => {
        this.wsitch = true;
        getConvertImagePath({
          filePath: this.goodsFilePath,
        }).then((res) => {
          this.PdfItems = res.data;
          this.PdfSize = res.data.length;
          if (this.Trial) {
            let num = this.PdfSize * 0.1;
            this.TrialPdfSize = num.toFixed();
            if (this.PdfSize < 10) {
              this.TrialPdfSize = 1;
            }
            this.PdfItems = this.PdfItems.slice(0, this.TrialPdfSize);
          }
          this.showEpub(readingLocation); //加载阅读器，传之前的阅读位置
          this.selectReadInfo(); //获取阅读时长接口
        });
      }, 100);
    },
    // 电子书的解析和渲染
    showEpub(readingLocation) {
      var that = this;
      setTimeout(function () {
        // $("#slider").slider({
        // 	value: that.PdfIndex,
        // 	min: 1,
        // 	max: that.PdfSize,
        // 	slide: function(event, ui) {
        // 		$(".flipbook").turn("page", ui.value)
        // 		that.PdfIndex = ui.value;
        // 	}
        // });
        setTimeout(function () {
          $(".flipbook").turn({
            width: window.innerWidth - 522,
            height: window.innerHeight - 130,
            elevation: 50,
            // gradients: true,
            autoCenter: true,
            when: {
              turned: function (e, page) {
                that.PdfIndex = page;
              },
            },
          });
        }, 300);
        if (readingLocation > 1) {
          setTimeout(() => {
            $(".flipbook").turn("page", readingLocation);
          }, 600);
        }
        setTimeout(() => {
          let next = document.getElementById("next");
          let keyListener = function (e) {
            // 左键
            if ((e.keyCode || e.which) == 37) {
              $(".flipbook").turn("previous");
            }
            // 上键
            if ((e.keyCode || e.which) == 38) {
              $(".flipbook").turn("previous");
            }
            // 右键
            if ((e.keyCode || e.which) == 39) {
              $(".flipbook").turn("next");
              if (Number(that.TrialPdfSize) == that.PdfIndex && that.Trial) {
                that.$message("该商品试读结束");
                this.checkIsBuys();
                return;
              }
            }
            // 下键
            if ((e.keyCode || e.which) == 40) {
              $(".flipbook").turn("next");
              if (Number(that.TrialPdfSize) == that.PdfIndex && that.Trial) {
                that.$message("该商品试读结束");
                this.checkIsBuys();
                return;
              }
            }
          };
          document.addEventListener("keyup", keyListener, false);
        }, 800);

        that.loading.close();
      }, 100);
    },
    //加入书架
    addBookshelf() {
      //判断是否登录
      let userId = this.$store.getters.userId;
      if (!userId) {
        this.$store.commit("SET_REDIRECTPARAMS", {
          goodsId: this.$route.query.goodsId,
          relevanceId: this.$route.query.relevanceId,
          resource: this.$route.query.resource,
          resourceType: this.$route.query.resourceType,
        });
        this.$router.push({
          path: "/login",
          query: {
            redirectPath: "/onlineReading",
          },
        });
      } else {
        if (!this.isBookshelf) {
          SelGoodsStatus(this.$route.query.goodsId).then((goodsState) => {
            if (goodsState.data.status === 2) {
              BookshelfAdd({
                goodsId: this.$route.query.goodsId,
                type: this.$route.query.resourceType,
              }).then((res) => {
                if (res.code == 200) {
                  this.$message({
                    message: "加入书架成功",
                    type: "success",
                  });
                  this.isBookshelf = true;
                }
              });
            } else {
              this.$message("该商品已下架");
            }
          });
        } else {
          if (this.$route.query.typeName == 0) {
            return;
          }
          //移出书架
          delBookshelf({
            goodsId: this.$route.query.goodsId,
            type: this.$route.query.resourceType,
          }).then((res) => {
            if (res.data) {
              this.isBookshelf = false;
              this.$message({
                message: "移出书架",
                type: "success",
              });
            }
          });
        }
      }
    },
    //全屏阅读
    fullScreen() {
      this.loading = this.$loading({
        lock: true,
      });
      screenfull.toggle();
      this.wsitch = false;
      setTimeout(() => {
        this.wsitch = true;
        setTimeout(() => {
          this.tabbook(this.item, this.index);
          // this.loading.close()
        }, 100);
      }, 100);
    },
    previous() {
      $(".flipbook").turn("previous");
    },
    next() {
      if (Number(this.TrialPdfSize) == this.PdfIndex && this.Trial) {
        this.$message("该商品试读结束");
        this.checkIsBuys();
        return;
      }
      $(".flipbook").turn("next");
    },
    result() {
      this.changezt({
        backclass: this.backclass,
      });
      var hei = this.backclass === "zt_heise" ? "#ccc" : "#000";
    },
    changezt(item) {
      //更换主题
      this.backclass = item.backclass;
      this.$store.state.read.backclass = item.backclass;
    },
    shezhisave() {
      setTimeout(() => {
        var shezhisave = {
          num: this.num, //字体
          backclass: this.backclass, //主题
          zwzt: this.zwzt, //字体
        };
        localStorage.setItem("shezhisave", JSON.stringify(shezhisave));
      }, 300);
    },
  },
};
</script>


